import React, { Component } from 'react'
import { MenuWrapper, MenuButton,MenuDrawer } from './menu.style';
import { Button,Icon} from 'antd';
import menuList from './menuList';
import Scroll from 'react-scroll';

const Link = Scroll.Link;

export default class Menu extends Component {

    state = {
        visible:false
    }

    
    toggleDrawer = () => {
        const visible = !this.state.visible;
        this.setState({visible})
    }
    onClose = () => {
        this.setState({
          visible: false,
        });
      };




    render() {
        
        return (
            <MenuWrapper>
               
                    <div className="fullsize-menu">
                        { menuList.map((item, key) => 
                         (
                            (
                                <Link activeClass="active" key={key} to={item.scrollTarget} spy={true} smooth={true} offset={-150} duration={500} >
                                    <MenuButton type="link" size="small" icon={item.icon} >{item.name} </MenuButton>
                                </Link>
                            )
                        ) 
        
                    )}
                    </div>
                
                        <div className="mobile-size-menu">
                            <Button type="primary" onClick={this.toggleDrawer} >
                                <Icon type={!this.state.visible ? 'menu-unfold' : 'menu-fold'} />
                            </Button>
                            <MenuDrawer
                                placement="top"
                                closable={true}
                                onClose={this.onClose}
                                visible={this.state.visible}
                                className="menu-drawer"
                            >
                                {
                                    menuList.map((item, key) => {
                                        return  (
                                            <Link className="link" activeClass="active" key={key} to={item.scrollTarget} spy={true} smooth={true} offset={-150} duration={500} >
                                                <MenuButton
                                                    onClick={()=>this.onClose()}
                                                    type="link" size="small" icon={item.icon} >{item.name}
                                                </MenuButton>
                                            </Link>
                                        )
                                    })
                                }
                                </MenuDrawer>
                            </div>
                
                
            </MenuWrapper>
        )
    }
}
