import React from 'react'
import {ImageSlider,SliderWrapper} from './slider.style';
import cowImage from '../../Images/slider-cow.jpg';
import cookieImage from '../../Images/slider-cookie.jpg';
import sweetsImage from '../../Images/slider-sweets.jpg';
import LazyLoad from 'react-lazyload';


const Slider = () => {
    return (
        <SliderWrapper>

            <ImageSlider autoplay effect="scrollx">
                
                    <LazyLoad height={650} scroll>
                        <img src={cowImage} alt="Loading" />
                    </LazyLoad>   
                
                    <LazyLoad height={650} scroll>
                        <img src={cookieImage} alt="Loading" />
                    </LazyLoad>
                    
                    <LazyLoad height={650} scroll>
                        <img src={sweetsImage} alt="Loading" />
                    </LazyLoad>
                
            </ImageSlider>
        </SliderWrapper>
    )
}

export default Slider
